	import {mapState, mapActions} from 'vuex'

	export default {
		data() {
			return {
				title: 'Hello'
			}
		},
		computed: {
    ...mapState('user', {
      userInfo: state => state.userInfo,
    })
  },
		onLoad() {
			this.userLogin()
		},
		methods: {
			...mapActions('user', ['changeUserInfo']),
			navigateTo () {
				
			},
			userLogin () {
				this.api.userLogin({}).then(res => {
					console.log(res)
					this.changeUserInfo(res)
				})
			}
		}
	}
